@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup{

    background: linear-gradient(135deg, v.$azulElectrico, v.$morado, v.$verdeNeon);
  
    h3 , .dia{
        text-align: center;
        color: v.$blanco;
    }

    .dia{
        font-size: 3rem;
        text-transform: uppercase;
    }

}

.escenarios-contenedor{

    @include m.contenedor;

    @include m.tablet{
        @include m.grid(2);
    }

}

.escenario{

    padding-top: 2rem;
    border-radius: 1rem;

    .nombre-escenario{
        font-size: 3rem;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
    }

    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out, filter 0.3s ease-in-out;

}

@include m.desktop{
    .escenario:hover{
        transform: scale(1.2) rotate(5deg);
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
        filter: brightness(1.2);
    }
}


.calendario{
    @include m.resetear-lista;

    li{
        padding: 2.5rem;
        font-weight: 700;

        span{
            font-weight: 400;
        }
    }

    li:hover{
        transform: scale(1.2)rotate(-5deg);
    }
}

.bg-amarillo{
    background-color: v.$amarilloElectrico;

    li:nth-child(even){
        background-color: v.$amarilloElectrico;
    }

    li:nth-child(odd){
        background-color: lighten(v.$amarilloElectrico, 5%);
    }
}

.bg-verde{
    background-color: v.$verde;

    li:nth-child(even){
        background-color: v.$verde;
    }

    li:nth-child(odd){
        background-color: lighten(v.$verde, 5%);
    }
}
